body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol,
ul {
  padding-left: 30px;
}
li {
  /*font-weight: lighter;*/
}
li.current,
li.current > button {
  font-weight: bold;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}
.square.winner {
  background-color: greenyellow;
  -webkit-transition: background-color 0.3s ease-out;
  transition: background-color 0.3s ease-out;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.readme-viewer {
  /* move to right and put it over others */
  left: auto;
  right: 5px;
  z-index: 10;
  width: 6.7em;
  height: 1.6em;
}
.readme-viewer.expanded,
.readme-viewer .wrapper {
  height: 400px;
  width: 800px;
}
.readme-viewer .wrapper {
  padding-left: 10px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  overflow-x: scroll;
  overflow: auto;
}
.readme-viewer button:first-of-type {
  margin-left: -10px;
}
